
import { defineComponent, ref } from '@vue/composition-api';

export default defineComponent({
  render() {
    return <el-popover
      popper-class="standard-view-popper"
      placement="bottom-end"
      width="170"
      visible-arrow={false}
      trigger="click">
      <el-button slot="reference" circle class="standard-view-switch">
        <i class="iconfont  iconliushitu" />
      </el-button>
      <div class="standard-view-btn-wrap">
        <span class="standard-view-btn"></span>
        <span class="standard-view-btn"></span>
        <el-button plain class={[this.standardView === 'top' ? 'is-selected' : '', 'standard-view-btn']}
          onclick={() => this.setStandardView('top')}>{this.$t('model.detail.standard_view.top')}</el-button>
        <span class="standard-view-btn"></span>
        <el-button plain class={[this.standardView === 'back' ? 'is-selected' : '', 'standard-view-btn']}
          onclick={() => this.setStandardView('back')}>{this.$t('model.detail.standard_view.back')}</el-button>
        <el-button plain class={[this.standardView === 'left' ? 'is-selected' : '', 'standard-view-btn']}
          onclick={() => this.setStandardView('left')}>{this.$t('model.detail.standard_view.left')}</el-button>
        <el-button plain class={[this.standardView === 'front' ? 'is-selected' : '', 'standard-view-btn']}
          onclick={() => this.setStandardView('front')}>{this.$t('model.detail.standard_view.front')}</el-button>
        <el-button plain class={[this.standardView === 'right' ? 'is-selected' : '', 'standard-view-btn']}
          onclick={() => this.setStandardView('right')}>{this.$t('model.detail.standard_view.right')}</el-button>
        <span class="standard-view-btn"></span>
        <span class="standard-view-btn"></span>
        <el-button plain class={[this.standardView === 'bottom' ? 'is-selected' : '', 'standard-view-btn']}
          onclick={() => this.setStandardView('bottom')}>{this.$t('model.detail.standard_view.bottom')}</el-button>
        <span class="standard-view-btn"></span>
      </div>
    </el-popover>;
  },
  props: {
    d4: {
      type: Object,
      required: true,
    },
  },
  setup(props, { root: { $nextTick } }) {
    const standardView = ref('');
    function setStandardView(view: string) {
      standardView.value = view;
      // 需要放到nextTick里，否则首次点击切换视角不能正确改变按钮选中状态
      $nextTick(() => {
        standardView.value = view;
      });
      props.d4 && props.d4.setCameraViewAngle(view);
    }
    return {
      standardView,
      setStandardView,
    };
  },
});
