
import { getModelCTMList } from '@/api/shoe';
import loadD4 from '@/utils/load-d4';
import { normalizeComponentsAndMaterial } from '@/utils/normalize-component';
import {
  defineComponent, ref, onMounted, onUnmounted,
} from '@vue/composition-api';
import resizeWatcher from '@/utils/resize-watch';
import { debounce } from 'throttle-debounce';
import SixViewSelector from './SixViewSelector.vue';

export default defineComponent({
  name: 'ModelReview',
  props: {
    modelId: {
      type: String,
      required: true,
    },
    workspaceId: {
      type: String,
    },
  },
  render() {
    return <div class="container">
      <canvas ref="checker" ondragover={(e: DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
      }} ondrop={(e: DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
      }} on={this.canvasEvent} />
      <SixViewSelector d4={this.d4} class="six-viewer"></SixViewSelector>
    </div>;
  },
  setup(props, { refs, root: { $set } }) {
    // eslint-disable-next-line
    const canvasEvent = ref<Record<string, (e: Event) => void>>({});
    const d4 = ref<Record<string, any>>({});
    // eslint-disable-next-line prefer-arrow-callback
    const resize = debounce(500, function resize() {
      if (d4.value) {
        const canvasDom = refs.checker as HTMLCanvasElement;

        const params = {
          // canvas: canvasDom,
          width: canvasDom.parentElement!.clientWidth,
          height: canvasDom.parentElement!.clientHeight,
          resolution: window.devicePixelRatio,
          // viewMode: 'modelCheck',
        };
        console.log(params.width, params.height);
        // canvasDom.width = params.width;
        // canvasDom.height = params.height;
        d4.value.resizeRenderer(params.width, params.height, params.resolution);
      }
    });
    const unmounted = ref<() => void>();
    onMounted(async () => {
      const getModelData = async () => {
        const { components: ctms } = await normalizeComponentsAndMaterial(
          ((await getModelCTMList(props.modelId, props.workspaceId)).data ?? []),
        );
        return ctms;
      };
      const [data, D4] = await Promise.all([
        getModelData(),
        loadD4(),
      ]);
      const checker = refs.checker as HTMLCanvasElement;
      d4.value = new D4({
        canvas: checker,
        width: checker.parentElement!.clientWidth,
        height: checker.parentElement!.clientHeight,
        resolution: window.devicePixelRatio,
        viewMode: 'modelCheck',
      });
      await d4.value.loadModel(data);
      const event = {
        keydown(e: KeyboardEvent) {
          d4.value.shortcutKeys(e);
        },
        mousedown(e: MouseEvent) {
          const controlsMouseMove = (e2: MouseEvent) => {
            d4.value.controlsMouseMove(e2);
            d4.value.mouseMoveRender(e2);
          };

          function controlsMouseUp() {
            d4.value.controlsMouseUp();
            d4.value.render();
            document.removeEventListener('mousemove', controlsMouseMove, false);
            document.removeEventListener('mouseup', controlsMouseUp, false);
          }

          const isStart = d4.value.controlsMouseDown(e);
          if (isStart) {
            document.addEventListener('mousemove', controlsMouseMove, false);
            document.addEventListener('mouseup', controlsMouseUp, false);
          }
        },
        wheel: (e: WheelEvent) => {
          e.preventDefault();
          e.stopPropagation();

          d4.value.controlsMouseWheel(e);
          d4.value.render();
        },
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        touchstart: (e2: TouchEvent) => {
          const controlsTouchMove = (e: TouchEvent) => {
            d4.value.controlsTouchMove(e);
          };

          function controlsTouchEnd(e: TouchEvent) {
            d4.value.controlsTouchEnd(e);

            document.removeEventListener('touchmove', controlsTouchMove, false);
            document.removeEventListener('touchend', controlsTouchEnd, false);
          }

          document.addEventListener('touchmove', controlsTouchMove, false);
          document.addEventListener('touchend', controlsTouchEnd, false);
        },
        contextmenu: (e: Event) => {
          e.preventDefault();
          e.stopPropagation();
        },
      };
      const keys = Object.keys(event) as ('keydown' | 'mousedown' | 'wheel' | 'touchstart')[];
      keys.forEach(key => $set(canvasEvent.value, key, event[key]));
      unmounted.value = resizeWatcher(() => resize());
      console.log(unmounted.value);
    });
    onUnmounted(() => {
      // @ts-ignore
      d4.value = null;
      if (unmounted.value) {
        unmounted.value();
      }
    });
    return {
      d4,
      canvasEvent,
    };
  },
});
